import React from "react";

export const Create = () => {
  return (
    <div>
      <div>
        <label>Add your own recipe</label>
      </div>
      <div>
        <input placeholder="Title"></input>
      </div>
      <div>
        <input placeholder="Dish types"></input>
      </div>
      <div>
        <input placeholder="Image"></input>
      </div>
      <div>
        <input placeholder="Summary"></input>
      </div>
      <div>
        <input placeholder="Score"></input>
      </div>
      <div>
        <input placeholder="Health score"></input>
      </div>
      <div>
        <input placeholder="Instructions"></input>
      </div>
      <div>
        <select>
          <option>Vegan</option>
          <option>Gluten Free</option>
        </select>
      </div>
      <div>
        <button type="submit">Crear</button>
      </div>
    </div>
  );
};
